"use client";

import { useState, useEffect, useMemo } from 'react';
import { format } from 'date-fns';
import { Settings2, ArrowUpDown, ExternalLink } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Skeleton } from '@/components/ui/skeleton';
import { Checkbox } from '@/components/ui/checkbox';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { cn } from '@/lib/utils';

export function BillingTableSkeleton() {
  return (
    <div className="glass-card rounded-xl overflow-hidden border border-border/50">
      <div className="p-4 space-y-4">
        {[...Array(6)].map((_, i) => (
          <Skeleton key={i} className="h-14 w-full rounded-lg" />
        ))}
      </div>
    </div>
  );
}

export function BillingTable({ bills, onBillClick }: { bills: any[], onBillClick: (b: any) => void }) {
  const [visibleColumns, setVisibleColumns] = useState({ client: true, department: true, received: true });
  const [sortField, setSortField] = useState('date');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('asc');

  useEffect(() => {
    const saved = localStorage.getItem('billing_col_visibility');
    if (saved) setVisibleColumns(JSON.parse(saved));
  }, []);

  const toggleCol = (col: keyof typeof visibleColumns) => {
    const next = { ...visibleColumns, [col]: !visibleColumns[col] };
    setVisibleColumns(next);
    localStorage.setItem('billing_col_visibility', JSON.stringify(next));
  };

  const formatCurrency = (amount: number) => 
    new Intl.NumberFormat('en-BD', { style: 'currency', currency: 'BDT', maximumFractionDigits: 0 }).format(amount);

  const getDeptBadge = (name: string) => {
    const n = name?.toLowerCase() || "";
    if (n.includes("software")) return { label: "SD", color: "bg-blue-500/10 text-blue-500 border-blue-500/20" };
    if (n.includes("survey")) return { label: "SV", color: "bg-purple-500/10 text-purple-500 border-purple-500/20" };
    if (n.includes("planning")) return { label: "PL", color: "bg-orange-500/10 text-orange-500 border-orange-500/20" };
    return { label: "??", color: "bg-muted text-muted-foreground" };
  };

  const sortedBills = useMemo(() => {
    return [...bills].sort((a, b) => {
      const valA = sortField === 'date' ? new Date(a.tentativeBillingDate).getTime() : a[sortField];
      const valB = sortField === 'date' ? new Date(b.tentativeBillingDate).getTime() : b[sortField];
      return sortOrder === 'asc' ? (valA > valB ? 1 : -1) : (valA < valB ? 1 : -1);
    });
  }, [bills, sortField, sortOrder]);

  return (
    <div className="glass-card rounded-xl overflow-hidden border border-border/50">
      <div className="flex justify-end p-2 border-b bg-muted/10">
        <Popover>
          <PopoverTrigger asChild>
            <Button variant="ghost" size="sm" className="h-8 gap-2 text-xs font-bold"><Settings2 size={14} /> Columns</Button>
          </PopoverTrigger>
          <PopoverContent className="w-48 p-3" align="end">
            <p className="text-[10px] font-black uppercase text-muted-foreground mb-3">Visible Columns</p>
            {Object.keys(visibleColumns).map(col => (
              <div key={col} className="flex items-center space-x-2 mb-2">
                <Checkbox id={col} checked={visibleColumns[col as keyof typeof visibleColumns]} onCheckedChange={() => toggleCol(col as keyof typeof visibleColumns)} />
                <label htmlFor={col} className="text-sm capitalize cursor-pointer">{col}</label>
              </div>
            ))}
          </PopoverContent>
        </Popover>
      </div>

      <div className="overflow-x-auto">
        <table className="w-full text-left border-collapse">
          <thead className="bg-muted/30 text-[10px] font-black uppercase text-muted-foreground border-b border-border/50">
            <tr>
              <th className="p-4">Milestone / Project</th>
              {visibleColumns.client && <th className="p-4">Client</th>}
              {visibleColumns.department && <th className="p-4">Dept</th>}
              <th className="p-4 cursor-pointer hover:text-primary" onClick={() => { setSortField('date'); setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc'); }}>
                Due Date <ArrowUpDown size={10} className="inline ml-1"/>
              </th>
              <th className="p-4">Bill Amount</th>
              {visibleColumns.received && <th className="p-4">Received</th>}
              <th className="p-4">Status</th>
              <th className="p-4 text-right">Action</th>
            </tr>
          </thead>
          <tbody className="text-sm">
            {sortedBills.map((bill) => {
              const dept = getDeptBadge(bill.project.department?.name);
              return (
                <tr key={bill.id} className="border-b border-border/50 hover:bg-muted/5 cursor-pointer transition-all group" onClick={() => onBillClick(bill)}>
                  <td className="p-4">
                    <p className="font-bold text-foreground leading-none mb-1 group-hover:text-primary transition-colors">{bill.billName}</p>
                    <p className="text-[10px] text-muted-foreground uppercase font-medium">{bill.project.projectName}</p>
                  </td>
                  {visibleColumns.client && <td className="p-4 text-muted-foreground text-xs">{bill.project.client?.name}</td>}
                  {visibleColumns.department && (
                    <td className="p-4">
                      <Badge variant="outline" className={cn("text-[10px] font-black border", dept.color)}>{dept.label}</Badge>
                    </td>
                  )}
                  <td className="p-4 text-xs font-medium">
                    {bill.tentativeBillingDate ? format(new Date(bill.tentativeBillingDate), 'MMM dd, yyyy') : 'N/A'}
                  </td>
                  <td className="p-4 font-bold text-foreground">{formatCurrency(Number(bill.billAmount))}</td>
                  {visibleColumns.received && <td className="p-4 text-success font-bold">{formatCurrency(Number(bill.receivedAmount))}</td>}
                  <td className="p-4">
                    <Badge variant="outline" className={cn(
                      "text-[10px] font-black",
                      bill.status === 'PAID' ? "bg-success/10 text-success border-success/30" : 
                      bill.status === 'PARTIAL' ? "bg-amber-500/10 text-amber-600 border-amber-500/30" : "bg-muted text-slate-500"
                    )}>{bill.status}</Badge>
                  </td>
                  <td className="p-4 text-right">
                    <ExternalLink size={14} className="ml-auto opacity-20 group-hover:opacity-100 transition-opacity" />
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}